<template>
    <div class="weui-panel weui-panel_access">
        <div class="weui-panel__hd">{{title}}</div>
        <div class="weui-panel__bd">
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        title:String
    }
}
</script>



