<template>
    <section class="m-list" ref="car-list">
        <div class="banner-img"><slot></slot></div>
        <ul class="car-list" v-if="carList.length != 0">
            <li class="car-item" v-for="(item,index) in carList" :key="index">
                <div class="car-img">
                    <img :src="item.icon" alt="">
                </div>
                <div class="car-desc">
                    <div class="car-name">{{`${item.name}`}}</div>
                    <div class="car-price">
                        <div class="car-price" v-if="item.nowPrice">
                            <span>{{`${item.nowPrice}`}}</span>
                            <span>{{`${item.primePrice}`}}</span>
                            <div class="car-labels" v-if = "item.labels">
                                <span class="label" v-for="(item,index) in getlabel" :key="index">
                                    {{item}}
                                </span>
                            </div>
                        </div>
                         <div class="car-price" v-if="item.firstPayment">
                            <span><del>{{`${item.firstPayment}`}}</del></span>
                            <span>{{`${item.monthPayment}`}}</span>
                            <span class="period" v-if="item.period">{{`X${item.period}`}}</span>
                        </div>                        
                    </div>
                </div>
            </li>

        </ul>
    </section>
</template>

<script>
export default {
    props:{
        carList:Array
    },
    data:function(){
        return{
            
        }
    },
    mounted:function(){

    },
    methods:{
        
    }
}
</script>



