<template>   
    <section class="m-home">
         <v-header></v-header>
         <v-slider v-if="sliderArr.length != 0">
             <div class="slider-item" v-for="(item,index) in sliderArr" :key="index">
                 <a :href="item.realUrl">
                     <img :src="item.imgUrl" alt="">
                 </a>
             </div>
         </v-slider>
         <v-nav></v-nav>
         <v-panel :title="brand" v-if="brandArr.length != 0">
             <a  class="brand-item" v-for="(item,index) in brandArr" :key="index" @click="carRouter(item.id)">
                 <div class="img">
                     <img :src="item.brandIcon" alt="">
                 </div>                
                 <p>{{item.brand}}</p>
             </a>
             <a href="" class="brand-item all">
                 <div class="img">
                    <i class="icon-more "></i>
                 </div>
                 <p></p>
             </a>
         </v-panel>
         <v-list :carList ="leaseCarList">
             <div class="car-banner" v-if="Object.keys(leaseCarbanner).length !=0">
                  <a :href="leaseCarbanner.realUrl"><img :src="leaseCarbanner.imgUrl" alt=""></a>    
             </div>                    
         </v-list>
         <v-list :carList ="importCarList">
             <div class="car-banner" v-if="Object.keys(importCarbanner).length !=0">
                  <a :href="importCarbanner.realUrl"><img :src="importCarbanner.imgUrl" alt=""></a>    
             </div>                    
         </v-list>
         <v-list :carList ="secondCarList">
             <div class="car-banner" v-if="Object.keys(secondCarbanner).length !=0">
                  <a :href="secondCarbanner.realUrl"><img :src="secondCarbanner.imgUrl" alt=""></a>    
             </div>                    
         </v-list>
    </section>
</template>

<script>
import vHeader from '@/components/mobile/m-header'
import vSlider from '@/components/mobile/m-slider'
import vNav from '@/components/mobile/m-nav'
import vPanel from '@/components/mobile/m-panel'
import vList from '@/components/mobile/m-list'
import {MOBILE_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return{
            sliderArr:[],
            brand:"",
            brandArr:[],
            leaseCarList:[],
            importCarList:[],
            secondCarList:[],
            leaseCarbanner:{},
            secondCarbanner:{},
            importCarbanner:{}
        }
    },
    components:{
        vHeader,
        vSlider,
        vNav,
        vPanel,
        vList 
    },
    methods:{
        getHomeData:function(){
            let _this = this;
            $.ajax({
                url:MOBILE_COMMONURL+"/index",
                type:"get",
                data:{
                    'platForm':'020005'
                },
                dataType:'json',
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        _this.sliderArr = data.data.carousel;
                        _this.brandArr = data.data.brandList;
                        _this.leaseCarbanner = data.data.leaseCarImage;
                        _this.leaseCarList = data.data.leaseList;
                        _this.importCarbanner = data.data.importCarImage;
                        _this.importCarList = data.data.importList;
                        _this.secondCarbanner = data.data.secondHandCarImage;
                        _this.secondCarList = data.data.secondList;
                    }
                }
            })
        },
        carRouter:function(id){
            this.$router.push({
                name: 'mobileSearchCar',
                params: {
                    id: id
                }
            })
        }
    },
    mounted:function(){
        this.getHomeData();
    }
}
</script>


